<template>
  <v-container>
    <teams-nav></teams-nav>
    <team-info :team=team :teamNotes=teamNotes :message=message  :key="this.$route.params.tag"></team-info>
  </v-container>
</template>

<script>
import mixins from '@/mixins'
import TeamInfo from '../components/TeamInfo'
import TeamsNav from '../components/TeamsNav'

export default {
  mixins: [mixins],
  components: {TeamInfo,TeamsNav},
  data () {
    return {
      message: "This is a Team",
      teamName: "",
      teamTag: ""
    }
  },
  methods: {
    capitalize: function (string) {
      return string.charAt(0).toUpperCase() + string.slice(1);
    },
  },
  computed: {
    teamNotes: function () {
      var notes = this.$store.getters.filteredTeamNotes(this.teamName)
      return notes[0]
    },
    team: function () {
      var self = this
      return this.$store.getters.filteredTeam(self.teamTag)
    }
  },
  created () {
    this.teamName = this.capitalize(this.$route.params.tag)
    this.teamTag = "fc_" + this.$route.params.tag.toLowerCase()
    this.message = this.capitalize(this.$route.params.tag) + "Team"
  }
}

</script>


