<template>
  <v-container>
  <div class="grid-container">
    <div 
      v-for="item in navItems" 
      :class= getCssClass(item)
      :key="item.name"
      @click="goToPath(item.path)"
      >
      {{item.name}}
      </div>
  </div>
  </v-container>
</template>

<script>

  export default {
    data () {
      return {
        message: "Teams Nav Pages"
      }
    },
    computed: {
      navItems: function() {
        return this.$store.getters.teamsNavItems
      }
    },
    methods: {
      goToPath (path) {
        this.$router.push(path)
      },
    getCssClass(item) {
        return "grid-item pointer " + item.color
      } 
    },
  }
</script>

<style>

.grid-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  grid-gap: 5px;
  grid-auto-rows: minmax(50px, auto);
  align-content: stretch;
}

.grid-item {
  padding:10px;
  color: white
}

.pointer {
  cursor: pointer;
}

.center-text {
  text-align: center
}

</style>
