<template>
  <v-container>
    <div class="document">
      <h1>{{message}}</h1>
      <p><span class="bold">Team Members:</span>
        <span v-for="(member, index) in team" :key=index>
          {{ member.FNAME }}&nbsp;{{ member.LNAME }}&nbsp;{{ member.MOBILEPHONE }}&nbsp;<span @click=mailTo(member.EMAIL) class="link">{{member.EMAIL}}</span> ; 
        </span>
      </p>
        <p v-html="teamNotes.PARAGRAPH"></p>
    </div>
  </v-container>
</template>

<script>
  import mixins from '@/mixins'

  export default {
    props: ['teamNotes','team','message'],
    mixins: [mixins],
  }
</script>